

import Vue from 'vue'

import Vuex from 'vuex'

import axios from 'axios'


Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		flag: false,
		flagRight: false,
		index: null,
		initialSlide:null,
		HomeComponent: {
			home: {
				swiperSlides: [],
				specialArr: [],
				HomeAritcal: [],
				specialAreaContent: [],
				HotSaleProduct: [],
				NewProduct: [],
				RecommendProduct: [],
				Product: [],

				adversitingone: [
					{
						Url: "",
						ImageUrl: ""
					}
				],
				adversitingtwo: [
					{
						Url: "",
						ImageUrl: ""
					}
				],
				adversitingthree: [
					{
						Url: "",
						ImageUrl: ""
					}
				]
			},
			slgs: {
				swiperSlides: [],
				categoryArr: [],
				categoryAreaArr: []
			}
		}
	},
	getters: {
	},
	mutations: {
		changeFlag: function (state, id) {
			state.flag = id.state
		},
		changeFlagRIght: function (state, id) {
			state.flagRight = id.state
		},
		getHome: function (state, id) {
			var This = this;
			axios.get("Member/loginSuccess?token=test").then(function (res) {
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOME_BANNER",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
							This.state.HomeComponent.home.swiperSlides.push(bannerObj);
						});
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "SPECIAL_AREA",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
							This.state.HomeComponent.home.specialAreaContent.push(bannerObj);
						});
					});

				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "PRODUCT",
						AdvertCode: "HOME_HOT",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						This.state.HomeComponent.home.HotSaleProduct = res.data.Data;

						This.state.HomeComponent.home.HotSaleProduct.map(function (data) {
							data.ImageUrl =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
						});
					});

				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOMT_ADVERTISING_SPACE_ONE",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						var Obj = {};
						Obj.Url = res.data.Data[0].Url;
						Obj.ImageUrl =
							JSON.parse(res.data.Data[0].ImageUrl)[0].ServerUrl +
							JSON.parse(res.data.Data[0].ImageUrl)[0].FilePath;
						This.state.HomeComponent.home.adversitingone = [];
						This.state.HomeComponent.home.adversitingone.push(Obj);
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOMT_ADVERTISING_SPACE_TWO",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						var Obj = {};
						Obj.Url = res.data.Data[0].Url;
						Obj.ImageUrl =
							JSON.parse(res.data.Data[0].ImageUrl)[0].ServerUrl +
							JSON.parse(res.data.Data[0].ImageUrl)[0].FilePath;
						This.state.HomeComponent.home.adversitingtwo = [];
						This.state.HomeComponent.home.adversitingtwo.push(Obj);
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOMT_ADVERTISING_SPACE_THREE",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						var Obj = {};
						Obj.Url = res.data.Data[0].Url;
						Obj.ImageUrl =
							JSON.parse(res.data.Data[0].ImageUrl)[0].ServerUrl +
							JSON.parse(res.data.Data[0].ImageUrl)[0].FilePath;
						This.state.HomeComponent.home.adversitingthree = [];
						This.state.HomeComponent.home.adversitingthree.push(Obj);
					});

				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "PRODUCT",
						AdvertCode: "HOMT_NEW_PRODUCT",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						This.state.HomeComponent.home.NewProduct = res.data.Data;

						This.state.HomeComponent.home.NewProduct.map(function (data) {
							data.ImageUrl =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
						});
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOME_SPECIAL_SPACE",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
							This.state.HomeComponent.home.specialArr.push(bannerObj);
						});
					});

				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "PRODUCT",
						AdvertCode: "HOME_RECOMMEND",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						This.state.HomeComponent.home.RecommendProduct = res.data.Data;

						This.state.HomeComponent.home.RecommendProduct.map(function (data) {
							data.ImageUrl =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
						});
					});

				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "PRODUCT",
						AdvertCode: "HOME_RECOMMEND",
						PageIndex: 1,
						PageSize: 3
					})
					.then(function (res) {
						This.state.HomeComponent.home.Product = res.data.Data;
						This.state.HomeComponent.home.Product.map(function (data) {
							data.ImageUrl =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
						});
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "HOMT_ARTICAL",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							This.state.HomeComponent.home.HomeAritcal = res.data.Data;
							data.ImageUrl =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
						});
					});
			});
		},
		getSlgs: function () {
			var This = this;
			axios.get("Member/loginSuccess?token=test").then(function (res) {
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "SLGS_BANNER",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
								This.state.HomeComponent.slgs.swiperSlides.push(bannerObj);
						});
						// console.log(This.swiperSlides);
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "SLGS_CATEGORY",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.Title = data.Title;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
								This.state.HomeComponent.slgs.categoryArr.push(bannerObj);
						});
					});
				axios
					.post("api/Advert/LoadAdvertList", {
						Kind: "IMAGE",
						AdvertCode: "SLGS_AREA",
						PageIndex: 1,
						PageSize: 100
					})
					.then(function (res) {
						res.data.Data.map(function (data) {
							var bannerObj = {};
							bannerObj.URL = data.Url;
							bannerObj.IMAGE =
								JSON.parse(data.ImageUrl)[0].ServerUrl +
								JSON.parse(data.ImageUrl)[0].FilePath;
								This.state.HomeComponent.slgs.categoryAreaArr.push(bannerObj);
						});
					});
			});
		}

	},
	actions: {
		getHome: function ({ commit, dispatch }, obj) {
			commit("getHome")
		},
		getSlgs: function ({ commit, dispatch }, obj) {
			commit("getSlgs")
		}
	},
});
export default store


