<template>
    <swiper class='Hcontainer' :options="HeaderSwiperOption" ref="mySwiper">
        <swiper-slide class='recommend'><router-link  tag='span' :to="{name:'home'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'slgs'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'zbls'}"></router-link></swiper-slide>
        <swiper-slide><router-link tag='span' :to="{name:'sxsp'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span'  :to="{name:'mcjs'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'lygh'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'jdjj'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'myzq'}"></router-link></swiper-slide>
        <swiper-slide ><router-link tag='span' :to="{name:'mzzq'}"></router-link></swiper-slide>
    </swiper>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "mySwiper",
  data() {
    return {
      HeaderSwiperOption: {
        freeMode: true,
        slidesPerView: "auto",
        on: {
          init() {
            // console.log(11)
          },
          click(e) {
            var navWidth = 0;
            for (var i = 0; i < this.slides.length; i++) {
              navWidth += parseInt(this.slides.eq(i).css("width"));
            }
            var clientWidth = parseInt(this.$wrapperEl.css("width"));
            var clickIndex = this.clickedIndex;
            var activeSlidePosition = this.slides[clickIndex].offsetLeft;
            var tSpeed = 300;
            var navSlideWidth = this.slides.eq(clickIndex).css("width");
            this.slides
              .eq(clickIndex)
              .find("span")
              .transition(tSpeed);
            if (clickIndex > 0) {
              this.slides.eq(clickIndex - 1).transition(tSpeed);
            }
            if (clickIndex < this.slides.length) {
              this.slides.eq(clickIndex + 1).transition(tSpeed);
            }
            var navActiveSlideLeft = this.slides[clickIndex].offsetLeft;
            this.setTransition(tSpeed);
            if (
              navActiveSlideLeft <
              (clientWidth - parseInt(navSlideWidth)) / 2
            ) {
              this.setTranslate(0);
            } else if (
              navActiveSlideLeft >
              navWidth - (parseInt(navSlideWidth) + clientWidth) / 2
            ) {
              this.setTranslate(clientWidth - navWidth);
            } else {
              this.setTranslate(
                (clientWidth - parseInt(navSlideWidth)) / 2 - navActiveSlideLeft
              );
            }
          }
        }
      }
    };
  }
};
</script>
<style lang='less'>
@rem: 46.875rem;
.Hcontainer {
  width: auto;
  background:white;
  .swiper-slide {
    width: 170/@rem;
    font-size: 40/@rem;
    margin: 16/@rem 12/@rem;
    box-sizing: border-box;
    text-align: center;
    span {
      display: inline-block;
      width: 100%;
      height: 100%;
      // border-bottom: 2px solid white;
      padding: 15/@rem 0;
    }
  }
  .recommend {
    width: 80/@rem;
    // border-bottom: 2px solid red;
  }
  .router-link-active {
    border-bottom: 2px solid red;
    color: red;
    font-weight: 580;
  }
}
</style>




