<template>
<div class='hotsale'>
  <div class='title'>
    
  </div>
  <swiper :options="HotSaleSwiper" >
    <swiper-slide v-for="(slide,index) in HotSaleProduct" :key="index">
        <img :src="slide.ImageUrl" alt="">
        <div class='hotsaleName'>
            <img src="@/assets/images/hot.png" alt="">
            
            </div>
        <div class='hotsaleTitle'>{{slide.Title}}</div>
        <div class='hotsalePrice'>{{slide.Param1}}</div>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
  </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "carrousel",
  props:['HotSaleProduct'],
  data() {
    return {
      HotSaleSwiper: {
        freeMode: true,
        slidesPerView: "auto"
      },
    };
  }
};
</script>
<style lang='less'>
@rem: 46.875rem;
img {
  width: 100%;
}
.hotsale {
  .title {
    width: 100%;
    height: 100/@rem;
    line-height: 100/@rem;
    text-align: center;
    font-size: 40/@rem;
    font-weight: 580;
    margin-bottom:10/@rem;
    background:white;
    color:#6e6e6e;
  }
  .swiper-slide {
    width: 230/@rem;
    height: 390/@rem;
    margin-right: 10/@rem;
    img {
      width: 230/@rem;
      height: 230/@rem;
    }
    div {
      font-size: 26/@rem;
      text-align: center;
    }
    .hotsaleName {
      // position: relative;

      img {
        width: 60/@rem;
        height: 26/@rem;
        display: inline-block;
        position: relative;
        left: 0;
        top: 0;
      }
    }
    .hotsaleTitle{
          overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
      font-size: 28/@rem;
      line-height: 40/@rem;
    }
    .hotsaleMemo {
      font-size: 26/@rem;
      line-height: 40/@rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
      
    }
    .hotsalePrice {
      line-height: 40/@rem;
      color: red;
      font-size: 28/@rem;
      font-weight: 500;
    }
  }
}
</style>




