<template>
  <div class='category'>
      <a :href="item.Url" v-for='(item,index) in categoryArr' :key='index'>
          <div class='categoryImg'>
            <img :src="item.IMAGE" alt="">
          </div>
          <div>
              {{item.Title}}
          </div>
      </a>
  </div>
</template>
<script>
export default {
  props: ["categoryArr"]
};
</script>
<style lang='less'>
@rem: 46.875rem;
.category {
  width: 100%;
  height: 360/@rem;
  a {
    text-align: center;
    background:white;
    display: block;
    width: 25%;
    height: 180/@rem;
    float: left;
    .categoryImg {
      height: 120/@rem;
      width: 100%;
      position: relative;
      img {
        padding:5px;
        box-sizing:border-box;
        position: absolute;
        bottom: 0;
        width: 90/@rem;
        left:((750-90*4)/8)/@rem;
      }
    }

    div {
      width: 100%;
      height: 60/@rem;
      line-height: 60/@rem;
      font-size: 30/@rem;
    }
  }
}
</style>




