<template>
  <div class='categoryArea'>
      <a :href="item.URL" v-for='(item,index) in categoryAreaArr' :key='index'>
          <img :src="item.IMAGE" alt="">
      </a>
  </div>
</template>
<script>
export default {
  props:['categoryAreaArr']
}
</script>

<style lang='less'>
@rem: 46.875rem;
.categoryArea{
    overflow: hidden;
    a{
        display:block;
        float:left;
        img{
            width:100%;
        }
        &:nth-of-type(1){
            width:100%;
        }
        &:nth-of-type(2){
            width:50%;
        }
        &:nth-of-type(3){
            width:50%;
        }
    }
}
</style>





