<template>
  <div id="app">
    <header-nav></header-nav>
    <!-- mode="out-in" -->
    <div class='container'>
    <swiper :options="AppSwiper"  ref='ASwiper'>
      <swiper-slide   v-if='flag'> 
        <router-view  name='left' ></router-view>
      </swiper-slide>
      <swiper-slide>
        <!-- :name='names' -->
          <transition    >
            <!-- class='center' -->
          <router-view class=''></router-view>
          </transition>
      </swiper-slide>
      <swiper-slide>
        <router-view  name='right' v-if='flagRight' ></router-view>
      </swiper-slide>
    </swiper >
    </div>
  </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import headerNav from "@/components/header-nav";
var html = document.documentElement;
var W = html.getBoundingClientRect().width;
html.style.fontSize = W / 16 + "px";
export default {
  name: "App",
  components: {
    headerNav
  },
  data() {
    return {
      This: this,
      names: "left",
      flag: true,
      flagRight: true,
      startIndex: null,
      endIndex: null,
      index: 0,
      AppSwiper: {
        initialSlide: 0,
        init() {},
        on: {
          touchMove: function() {},
          touchMove() {
            var activeIndex = this.activeIndex;
          },
          touchStart() {
            var activeIndex = this.activeIndex;
            this.App.startIndex = this.activeIndex;
          },
          transitionEnd() {
            // console.log(1)
            this.App.endIndex = this.activeIndex;
            // console.log(this.store.state.index)
            // console.log(this.App.startIndex, this.App.endIndex);
            if (this.App.endIndex > this.App.startIndex) {
              this.App.names = "none";
              this.router.push(
                this.router.options.routes[this.store.state.index + 1].path
              );
            } else if (this.App.endIndex == this.App.startIndex) {
            } else {
              this.router.push(
                this.router.options.routes[this.store.state.index - 1].path
              );
            }
          }
        }
      }
    };
  },
  watch: {
    $route(to, from) {
      if (this.$store.state.flag == "false") {
        this.flag = false;
      } else {
        this.flag = true;
      }
      if (this.$store.state.flagRight == "false") {
        this.flagRight = false;
      } else {
        this.flagRight = true;
      }
      if (this.names != "none") {
        if (to.meta.index < from.meta.index) {
          this.names = "right";
        } else {
          this.names = "left";
        }
      }
      if (this.$store.state.indexFlag) {
        this.swiper.slideTo(1, 0, false); //slide1
      } else {
        this.swiper.slideTo(0, 0, false); //slide1
      }
    }
  },
  created() {

    if (this.$store.state.flag == "false") {
      this.flag = false;
    } else {
      this.flag = true;
    }
    if (this.$store.state.flagRight == "false") {
      this.flagRight = false;
    } else {
      this.flagRight = true;
    }
  },
  computed: {
    swiper() {
      return this.$refs.ASwiper.swiper;
    },
    initialSlideData() {
      // return this.$store.state.index;
    }
  },
  mounted() {
    // console.log(this.initialSlideData, 22222);
    this.swiper.router = this.$router;
    this.swiper.App = this;
    this.swiper.route = this.$route;
    this.swiper.store = this.$store;
    if (this.$store.state.indexFlag) {
      this.swiper.slideTo(1, 0, false); //slide1
    } else {
      this.swiper.slideTo(0, 0, false); //slide1
    }
  }
};
</script>

<style >
.container {
  overflow: hidden;
}
.left-enter {
  transform: translateX(100%);
}
.left-enter-active,
.left-leave-active {
  transition: 1s;
}
.left-leave-to {
  transform: translateX(-100%);
}
.right-enter {
  transform: translateX(-100%);
}
.right-enter-active,
.right-leave-active {
  transition: 1s;
}
.right-leave-to {
  transform: translateX(100%);
}
.center {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
}
</style>



