<template>
<div class='homeArtical'>
    <div class='title'>
        
    </div>
  <swiper :options="articalSwiper" ref="ArticalSwiper" >
    <swiper-slide v-for="(slide,index) in HomeAritcal" :key="index">
        <a :href="slide.Url">
            <img :src="slide.ImageUrl" alt="">
            <div class='artiaclleft'>
                <div>
                    <div class='articaltitle'>{{slide.Title}}</div>
                    <div class='articaldescribe'>{{slide.Describe}}</div>
                </div>
             
            </div>
               <div class='articalright'>
                    <span>{{slide.Param1}}</span>
                </div>
        </a>
    </swiper-slide>
    <!-- <div class="swiper-pagination" v-if='this.bannerMess.length>1' slot="pagination"></div> -->
  </swiper>
</div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "articalSwiper",
  props: ["HomeAritcal"],
  data() {
    return {
      articalSwiper: {
        loop: true,
        // effect: "coverflow",
        slidesPerView: 1.3,
        centeredSlides: true,
        autoplay: {
          delay: 1500 //1
        },
        pagination: {
          //   el: ".swiper-pagination"
        },
        init() {}
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.ArticalSwiper.swiper;
    }
  }
};
</script>
<style lang='less'>
@rem: 46.875rem;
.homeArtical {
  .swiper-slide {
    a{
        display:block;
        color:black;
    }
    .artiaclleft {
        float: left;
      box-sizing: border-box;
      padding-top: 10/@rem;
      width: 400/@rem;
      height: 115/@rem;
    }
    .articaltitle {
      height: 44/@rem;
      line-height: 44/@rem;
      font-size: 32/@rem;
    }
    .articaldescribe {
      font-size: 28/@rem;
      height: 44/@rem;
      line-height: 44/@rem;
    }
    .articalright {
      box-sizing: border-box;
      padding-top: 10/@rem;
     height: 44/@rem;
      line-height: 44/@rem;
      width:160/@rem;
      text-align: right;
      font-size:32/@rem;
      color:red;
      float: right;
    }
  }
  .title {
    width: 100%;
    height: 100/@rem;
    line-height: 100/@rem;
    text-align: center;
    font-size: 40/@rem;
    font-weight: 580;
    margin-bottom: 10/@rem;
    background: white;
    color: #6e6e6e;
  }
  img {
    display: block;
    border-radius: 4px;
  }
}
</style>




