import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import home from '@/views/home'
import slgs from '@/views/slgs'
import zbls from '@/views/zbls'
import sxsp from '@/views/sxsp'
import mcjs from '@/views/mcjs'
import lygh from '@/views/lygh'
import jdjj from '@/views/jdjj'
import myzq from '@/views/myzq'
import mzzq from '@/views/mzzq'



Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      // component: home,
      components: {
        default: home,
        right: slgs,
      },
      meta: {
        index: 0,
        initialSlide:0
      }
    },
    {
      path: '/slgs',
      name: 'slgs',
      components: {
        left: home,
        default: slgs,
        right: zbls,
      },
      meta: {
        index: 1,
        initialSlide:1
      }
    },
    {
      path: '/zbls',
      name: 'zbls',
      components: {
        left: slgs,
        default: zbls,
        right: sxsp,
      },
      meta: {
        index: 2,
        initialSlide:1
      }
    },
    {
      path: '/sxsp',
      name: 'sxsp',
      components: {
        left: zbls,
        default: sxsp,
        right: mcjs,
      },
      meta: {
        index: 3,
        initialSlide:1
      }
    },
    {
      path: '/mcjs',
      name: 'mcjs',
      components: {
        left: sxsp,
        default: mcjs,
        right: lygh,
      },
      meta: {
        index: 4,
        initialSlide:1
      }
    },
    {
      path: '/lygh',
      name: 'lygh',
      components: {
        left: mcjs,
        default: lygh,
        right: jdjj,
      },
      meta: {
        index: 5,
        initialSlide:1
      }
    },
    {
      path: '/jdjj',
      name: 'jdjj',
      components: {
        left: lygh,
        default: jdjj,
        right: myzq,
      },
      meta: {
        index: 6,
        initialSlide:1
      }
    },
    {
      path: '/myzq',
      name: 'myzq',
      components: {
        left: jdjj,
        default: myzq,
        right: mzzq,
      },
      meta: {
        index: 7,
        initialSlide:1
      }
    },
    {
      path: '/mzzq',
      name: 'mzzq',
      components: {
        left: jdjj,
        default: myzq,
      },
      meta: {
        index: 8,
        initialSlide:1
      }
    },
  ]

})
router.beforeEach((to, from, next) => {

  this.a.app.$store.state.index = to.meta.index
  this.a.app.$store.state.indexFlag = to.meta.initialSlide
  
  if (to.meta.index == 0) {
    this.a.app.$store.commit('changeFlag', { state: 'false' })
  } else if (to.meta.index == this.a.options.routes.length - 1) {
    this.a.app.$store.commit('changeFlagRIght', { state: 'false' })
  } else {
    this.a.app.$store.commit('changeFlag', { state: 'true' })
    this.a.app.$store.commit('changeFlagRIght', { state: 'true' })
  }
  next()
})

export default router;


