<template>
  <div class='spectial' v-if="specialArr.length">
      <div class='left' >
          <a :href="specialArr[0].URL">
              <img :src="specialArr[0].IMAGE" alt="">
          </a>
      </div>
      <div class='right'>
          <a :href="specialArr[1].URL">
              <img :src="specialArr[1].IMAGE" alt="">
          </a>
            <a :href="specialArr[2].URL">
              <img :src="specialArr[2].IMAGE" alt="">
          </a>
      </div>
  </div>
</template>

<script>
export default {
  props: ["specialArr"],
  data() {
    return {};
  },
  watch: {
    specialArr: function() {
      // console.log(1);
    }
  }
};
</script>

<style lang='less'>
@rem: 46.875rem;
.spectial {
  width: 100%;
  height: 430/@rem;
  a {
    display: block;
  }
  .left {
    box-sizing: border-box;
    width: 50%;
    height: 100%;
    border-right: 2px solid #f4f4f4;
    float: left;
    a {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .right {
    width: 50%;
    box-sizing: border-box;
    height: 100%;
    float: left;
    a {
      width: 100%;
      height: (430/2)/@rem;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
      &:nth-of-type(1) {
        border-bottom: 1px solid #f4f4f4;
      }
    }
  }
}
</style>





