<template>
  <div class='specialarea'>
      <div class='title'></div>
      <a v-for="(item,index) in specialAreaContent" :key='index' :href='item.URL'>
          <img :src="item.IMAGE">
      </a>
  </div>
</template>
<script>
export default {
  props: ["specialAreaContent"],
  data() {
    return {
      specialareaArr: [
        {
          image:
            "http://gdtvimg.weixinmvp.com/res/gdtvimg/20170225/1d6b4abbdf934b6884e8747f44c64729.jpg.thumb.jpg",
          link: "http://www.baidu.com"
        },
        {
          image:
            "http://gdtvimg.weixinmvp.com/res/gdtvimg/20170225/1d6b4abbdf934b6884e8747f44c64729.jpg.thumb.jpg",
          link: "http://www.baidu.com"
        },
        {
          image:
            "http://gdtvimg.weixinmvp.com/res/gdtvimg/20170301/ff7097c35bc047d681a0d36842c33ab8.jpg.thumb.jpg",
          link: "http://www.baidu.com"
        },
        {
          image:
            "http://gdtvimg.weixinmvp.com/res/gdtvimg/20170225/1d6b4abbdf934b6884e8747f44c64729.jpg.thumb.jpg",
          link: "http://www.baidu.com"
        }
      ]
    };
  }
};
</script>
<style lang='less'>
@rem: 46.875rem;
.specialarea {
  margin-top: 10/@rem;
  width: 100%;
  height: 554/@rem;
//   background: white;
  .title {
    width: 100%;
    height: 100/@rem;
    line-height: 100/@rem;
    text-align: center;
    font-size: 40/@rem;
    font-weight: 580;
    margin-bottom:10/@rem;
    background:white;
    color:#6e6e6e;
  }
  a {
    width: 50%;
    height: (554/2)/@rem;
    float: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    img {
      width: 100%;
      height: 100%;
    }
    &:nth-of-type(1) {
      border-bottom: 1px solid #ededed;
    }
    &:nth-of-type(2) {
      border-bottom: 1px solid #ededed;
    }
  }
}
</style>





