<template>
    <div>
        <banner-component :bannerMess = 'McjsData.swiperSlides'></banner-component>
        <category :categoryArr = 'McjsData.categoryArr'></category>
        <categoryarea :categoryAreaArr = 'McjsData.categoryAreaArr'></categoryarea>
    </div>
</template>

<script>
import bannerComponent from "@/components/banner";

import category from "@/components/category";

import categoryarea from "@/components/categoryarea";

import axios from "axios";
export default {
  name: "mcjs",
  components: {
    bannerComponent,
    category,
    categoryarea
  },
  data() {
    return {
      swiperSlides: [],
      categoryArr: [],
      categoryAreaArr: []
    };
  },
  computed: {
    McjsData() {
      return this.$store.state.HomeComponent.slgs;
    }
  },
  created() {
    if (this.$store.state.HomeComponent.slgs.swiperSlides.length == 0) {
      this.$store.dispatch("getSlgs");
    }
  }
};
</script>

<style>

</style>





