<template>
<div>
  <swiper :options="bannerSwiper" ref="BannerSwiper" v-if='bannerMess.length>1'>
    <swiper-slide v-for="(slide,index) in bannerMess" :key="index">
        <a :href="slide.URL"><img :src="slide.IMAGE"></a>
    </swiper-slide>
    <div class="swiper-pagination" v-if='this.bannerMess.length>1' slot="pagination"></div>
  </swiper>
  <div v-else>
    <img :src="bannerMess[0].IMAGE" v-if='bannerMess[0]'>
  </div>
</div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "BannerSwiper",
  props: ["bannerMess"],
  data() {
    return {
      bannerSwiper: {
        loop: true,
        autoplay: {
          delay: 1500 //1
        },
        pagination: {
          el: ".swiper-pagination"
        },
        init(){
          console.log(1)
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.BannerSwiper.swiper;
    }
  },
};
</script>
<style lang='less'>
img {
  width: 100%;
}
</style>




