<template>
    <div class='new' >
        <div class='title'>
          
        </div>
        <a v-for='(item,index) in NewProduct' :key="index" :href="item.Url">
            <img :src="item.ImageUrl" alt="">
             <div class='newIMg'>
                 <img  src="@/assets/images/newproduct.png" alt="">
            </div>
            <div class='newTitle'>{{item.Title}}</div>
            <div class='newPrice'>{{item.Param1}}</div>
        </a>
    </div>
</template>
<script>
export default {
  props:['NewProduct'],
  data() {
    return {
    };
  }
};
</script>
<style lang='less'>
@rem: 46.875rem;
.new {
  overflow: hidden;
  .title {
    width: 100%;
    height: 100/@rem;
    line-height: 100/@rem;
    text-align: center;
    font-size: 40/@rem;
    font-weight: 580;
    margin-bottom:10/@rem;
    background:white;
    color:#6e6e6e;
  }
  a {
    text-decoration: none;
    width: ((750-80)/3)/@rem;
    height: 395/@rem;
    float: left;
    margin-left: 20/@rem;
    color:black;
    text-decoration:none;
    img {
      width: ((750-80)/3)/@rem;
      height:((750-80)/3)/@rem;
    }
    div {
      img {
        width: 60/@rem;
        height: 26/@rem;
        line-height: 40/@rem;
      }
    }
    .newIMg {
      height: 40/@rem;
      line-height: 40/@rem;
    }
    .newTitle {
      line-height:33/@rem;
       font-size: 28/@rem;
       height:66/@rem;
    //     overflow: hidden;
    // white-space: nowrap;
    // text-overflow: ellipsis;
    }
    .newPrice {
      font-size: 28/@rem;
      color:red;
      line-height:34/@rem;
      height:34/@rem;
    }
  }
}
</style>





